/** @format */

.tesla-battery {
	width: 1050px;
	margin: 0 auto;
	h1 {
		font-family: 'RobotoNormal';
		font-weight: 100;
		font-size: 38px;
		text-align: center;
		letter-spacing: 3px;
	}
	&__notice {
		margin: 20px 0;
		font-size: 15px;
		color: #666;
		line-height: 20px;
	}
}
.tesla-temp {
	float: left;
	width: 200px;
	padding: 0 40px;
	margin: 0 35px 0 0;
	border-left: 1px solid #ccc;
	border-right: 1px solid #ccc;
}
.tesla-controls {
	display: block;
	width: 100%;
}
